import React from 'react';
import styleContainer from '../common/Container.module.css';
import s from './Funny.module.css';

const Funny = () => {
    return (
        <div className={styleContainer.container}>
           <h1 className={s.funny_text}>Мнагауважаемый Эйчар! Вазьми миня на роботу!
               Я франтенд дивелопер, я знаю риакт джиес тинарники и много чего дугого...</h1>
           <h2>Готов работать за ЕДУ!</h2>
        </div>
    );
};

export default Funny;